
// Starter Icon

#toplevel_page_warp {
	
	.wp-menu-image { background: url(../images/starter-theme-icon.svg) no-repeat center center / 20px auto !important; }
	.wp-menu-image:before { content: none; }
	
	a:hover .wp-menu-image { background-image: url(../images/starter-theme-icon-hover.svg) !important; }
	a.current .wp-menu-image { background-image: url(../images/starter-theme-icon-active.svg) !important; }

}

// END Starter Icon



// TinyMCE Editor

.mce-panel.mce-menu { z-index: 999999 !important; }

// END TinyMCE Editor



// User Avatars

#wp-user-avatars-user-settings > .form-table > tbody > tr:last-child { display: none; }

// END User Avatars



// Update Manager

.wbcr-upm-plugin-status { display: none; }
.rml-update-notice { display: none; }

// END Update Manager



// Litespeed

#wp-admin-bar-litespeed-bar-manage,
#wp-admin-bar-litespeed-bar-setting,
#wp-admin-bar-litespeed-bar-imgoptm,
#wp-admin-bar-litespeed-purge-all-lscache,
#wp-admin-bar-litespeed-lscache-purge-all,
#wp-admin-bar-litespeed-purge-cssjs,
#wp-admin-bar-litespeed-purge-opcache,
#wp-admin-bar-litespeed-purge-ccss { display: none; }

// END Litespeed



// Plugins Goups

body.plugins-php .subsubsub {
	
	.recently_activated,
	.mustuse,
	.dropins { display: none; }
}

// END Plugins Groups



// Woocommerce Categories Images

body.taxonomy-product_cat table.wp-list-table th.check-column img {
    
    display: inline-block;
    width: 48px;
    height: auto;
    margin: 0;
    margin-left: 41px;
    margin-bottom: 10px;
    vertical-align: middle;
    border: 1px solid #eee;
 
}

// END Woocommerce Categories Images



// WP Backery

.wpb_switch-to-gutenberg { display: none !important; }

.vc_css-editor {
	
	.vc_layout-onion { display: none; }
	
	.vc_settings {
		width: 100%;
	
		> *:nth-child(-n+6),
		> *:nth-last-child(-n+2) { display: none; }
	}
}

.vc_ui-panel-window {
	.vc_ui-panel-header-container,
	.vc_ui-panel-content,
	.vc_ui-panel-footer-container { padding: 8px; }

	.vc_ui-panel-header-heading { margin-bottom: 8px !important; }
